button {
    border: none;
    background-color: rgb(60, 120, 209);
    font-size: 24px;
    color: rgb(255,255,255);
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    outline: none;
    transition: all ease-out 0.5s;
}

button:hover {
    background-image: linear-gradient(to right, #415ac5, #7758e9);

}


.equals {
    grid-column: 3/5;
    background-color: rgb(228,39,15);
}

.equals:hover {
    background-color: rgb(228,39,15);
}